$font-size-xs: .8rem;
footer, .footer {
  padding: 3.75rem 0;

  h6, .h6 {
    display: inline-block;
    color: $light;
    text-transform: uppercase;
    font-size: $font-size-xs;
    border-bottom: 1px solid $pink;
    line-height: $line-height-lg;
    font-weight: 800;
    letter-spacing: 6px;
  }

  h4, .h4 {
    color: $light;
    font-weight: 900;
    letter-spacing: 2px;
  }

  .nav {
    .nav-item {
      .nav-link {
        font-size: $font-size-xs;
        color: $light;
        letter-spacing: 1px;
      }
    }
  }
  a, svg {
    transition: all 0.2s ease-in-out;
    &:hover {
      color: $pink !important;
      fill: $pink;
    }
  }
}

@include media-breakpoint-up(md) {
  footer, .footer {
    padding: 6rem 0;
  }
}

@include media-breakpoint-up(lg) {
  footer, .footer {
    padding: 7.5rem 0;
  }
}
