//
//
// buttons.scss
//
//

$font-size-xs: .8rem;

@include media-breakpoint-down(sm) {
  .btn-lg {
    font-size: $font-size-base;
    padding: $btn-padding-y $btn-padding-x;
  }
}

@each $color, $value in $theme-colors {
  .btn-outline-#{$color} {
    border-color: rgba($value, .25);

    &:hover {
      background-color: rgba($value, .2);
      border-color: rgba(0, 0, 0, 0);
      color: $value;
    }
  }
}

.btn {
  > span {
    display: inline-block;
  }

  .icon {
    height: 1.25rem;

    &:last-child {
      &:not(:only-child) {
        margin-right: -.5rem;
        margin-left: .5rem;
      }
    }

    &:first-child {
      &:not(:only-child) {
        margin-left: -.5rem;
        margin-right: .5rem;
      }
    }
  }

  &.rounded-circle {
    padding: 0;
    width: $spacer*3;
    height: $spacer*3;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  &.flat-circle {
    border-radius: $spacer !important;
    padding: 0;
    width: $spacer*3.3;
    height: $spacer*1.5;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  &.btn-sm {
    &.rounded-circle {
      width: $spacer*1.7;
      height: $spacer*1.7;
    }
  }

  &.btn-lg {
    .icon {
      height: $spacer*2.25;
    }

    &.rounded-circle {
      width: $spacer*4.5;
      height: $spacer*4.5;

      .icon {
        height: 1.5rem;
      }
    }
  }

  &.btn-pink {
    @include button-variant($pink, $pink);
  }

  &.btn-outline-pink {
    @include button-outline-variant($pink);
    &:hover, &:focus, &:active {
      background-color: transparent !important;
    }
  }

  &.btn-subscribe {
    background: linear-gradient(180deg, rgba($gray-300, 0.5) 0%, rgba($white, 0) 100%);
    color: $pink;
    font-size: $font-size-xs;
    text-transform: uppercase;
    letter-spacing: 6px;
    font-weight: 800;
    border-radius: 0;
    &:focus {
      box-shadow: none;
    }
  }

  &.btn-more {
    letter-spacing: 2px;
    text-transform: uppercase;
    font-size: .85rem;
    border-radius: $spacer*2.25;
    font-weight: 500;
    padding: $btn-padding-y $btn-padding-x-lg;
  }
}

.btn[class*='outline-'] {
  &:focus {
    .icon {
      @include svgColor($white);
    }
  }
}

// Back to top
.btn-back-to-top {
  position: fixed;
  bottom: $spacer*2;
  right: $spacer*2;
  z-index: $zindex-fixed;
}

// Loading Spinner
.btn-loading {
  display: flex;
  white-space: nowrap;
  align-items: center;
  justify-content: center;

  &:not(.btn-loading-animate) {
    > span {
      vertical-align: baseline;
      display: block;
      line-height: 1;
    }
  }

  .icon {
    display: none;
    margin-top: -.5em;
    margin-bottom: -.5em;
  }

  &.btn-loading-animate {
    .icon {
      path {
        animation: spin .5s infinite linear;
        transform-origin: 12px;
      }

      display: inline-block;
    }
  }
}

@keyframes spin {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
