//
// icons.scss
//


.icon {
  flex-shrink: 0;
  width: auto;
  height: auto;

  &.rounded-circle {
    width: $spacer*3.375;
    height: $spacer*3.375;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  &.icon-outline-pink {
    border: 1px solid;
    border-color: rgba($pink, .5);
    background-color: rgba($pink, .02);

    &:hover {
      animation: pulse 2s infinite;
    }
  }
}

.animation-pulse {
  &:hover {
    .icon {
      animation: pulse 2s infinite;
    }
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0px rgba(222, 208, 207, 0.7);
  }
  100% {
    box-shadow: 0 0 0 15px rgba($pink, 0);
  }
}


