.bg-body-color {
    background: $body-color;
}

.bg-purple {
    background-color: $purple;
}

.text-pink {
    color: $pink;
}

.bg-pink {
    background-color: $pink;
}

@each $color,
$value in $theme-colors {
    .bg-#{$color}-alt {
        background-color: rgba($value, .1);
    }
}

.bg-white-alt{
    background-color: rgba($white, .1);
}
