.custom-control{
    label, input{
        cursor: pointer;
    }
    label{
        user-select: none;
    }
    &.text-small{
      label{
          &:before,&:after{
              top: 0;
          }
      }
    }
}

.form-group{
    position: relative;
    .custom-select{
        + .icon{
            position: absolute;
            transform: translateY(-50%);
            right: $spacer*.75;
            top: $spacer*1.5;
            pointer-events: none;
        }
        &.custom-select-sm{
            + .icon{
                top: $spacer*1.125;
            }
        }
        &.custom-select-lg{
            + .icon{
                top: $spacer*2.25;
                transform: translateY(-50%) scale(1.25);
            }
        }
    }
}

.form-control {
    border-radius: 0;
    font-size: $font-size-lg;
    border: 0;
    border-bottom: 2px solid rgba($gray-300,.5);
    &::placeholder {
        text-transform: uppercase;
        letter-spacing: 4px;
        color: $gray-500;
    }
    &:focus {
        border-color: rgba($gray-300,.5);
    }
}
